*, *:before, *:after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
    font-family: Montserrat, 'sans-serif';
    height: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}
.content {
    flex: 1 0 auto;
}
.footer {
    flex: 0 0 auto;
}

.header{
    background-color: #1F2326;
    padding: 30px 70px;

    .header_wrapper{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .header_main_menu{
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;

            .logo{
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-left: -30px;

                img{
                    display: block;
                    margin: 0 30px;
                }
            }

            .nav{
                .header_menu{
                    display: flex;
                    flex-direction: row;
                    list-style: none;
                    margin: 0;

                    a{
                        color: #eeeeee;
                        text-decoration: none;
                         padding: 20px;
                    }

                    .active{
                        font-weight: bold;
                    }
                }
            }
        }

        .header_user{
            p{
                a {
                    color: #eeeeee;
                    position: relative;
                    text-decoration: none;


                    &:before {
                        background-image: url(img/akali.png);
                        background-repeat: no-repeat;
                        border-radius: 50%;
                        content: '';
                        width: 40px;
                        height: 40px;
                        display: block;
                        position: absolute;
                        top: -12px;
                        left: -50px;
                    }
                }
            }
        }
    }
}

.main_content {
    background-image: url(img/background.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #ff4656;
    padding: 70px 0 100px;

    .container {
        width: 1260px;
        margin: 0 auto;

        .main_content_wrapper {
            display: flex;
            flex-direction: row;

            .main_content_item_text {
                width: 20%;
                margin-right: 100px;

                img {
                    display: block;
                    //width: 230px;
                    height: 500px;
                    object-fit: scale-down;
                }
            }

            .main_content_item {
                width: 20%;
                margin: 0 30px;
                position: relative;

                img {
                    display: block;
                    //width: 230px;
                    height: 500px;
                    background-color: #1f2326;
                    object-fit: scale-down;
                }

                &.bg_white {
                    img {
                        background-color: #eeeeee;
                    }
                }

                .icons {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    padding: 8px;
                    background: #1f2326;
                    border: 2px solid #f8f8f8;
                    position: absolute;
                    bottom: -20px;
                    right: -15px;

                    img {
                        display: block;
                        width: 25px;
                        height: 30px;
                        object-fit: scale-down;
                        margin: 0 13px;
                    }

                    &.bg_white {
                        background-color: #eeeeee;
                        border: 2px solid #1f2326;
                    }
                }

                .hero_name {
                    position: absolute;
                    top: 15px;
                    left: -15px;

                    img {
                        display: block;
                        width: 30px;
                        height: auto;
                        object-fit: scale-down;
                        background-color: transparent;

                    }
                }

            }
        }
    }

    .carausel_container {
        width: 1200px;
        margin-top: 150px;
        margin-left: -150px;
        position: relative;

        .owl-carousel {
            .carausel_item_wrapper {
                display: flex;
                flex-direction: row;
                align-items: stretch;
                height: 189px;



                .carausel_item {
                    background: #f8f8f8;
                    width: 250px;
                    padding: 0 20px;
                    position: relative;

                    h4 {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 32px;
                        color: #c4c4c4;
                        margin: 10px 0;
                    }

                    h6 {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        color: #000000;
                        margin: 0;
                    }

                    p {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 20px;
                        letter-spacing: 0.1em;
                        color: #1f2326;
                    }

                    .carausel_logo {
                        width: 130px;
                        height: 130px;
                        background-color: #ff4656;
                        position: absolute;
                        bottom: 0;

                        img {
                            display: block;
                            width: 100px;
                            height: 100px;
                            position: absolute;
                            top: -30px;
                            right: -30px;
                        }
                    }

                }
            }

            .owl-prev{
                display: none;
            }

            .owl-next{
                position: absolute;
                right: -150px;
                top: 0;
                background-color: #F8F8F8;
                width: 150px;
                height: 189px;
                border: 2px solid #000000;
                background-image: url(img/Polygon.png);
                background-repeat: no-repeat;
                background-position: center center;
            }
        }
    }
}

.footer{
    background: #F8F8F8;
    padding-top: 60px;
}

@media (max-width: 780px){
    .header{
        padding: 10px 20px;

        .header_wrapper{
            .header_main_menu{
                .logo{
                    margin-left: -10px;

                    img{
                        display: block;
                        margin: 0 10px;
                    }
                }

                .nav{
                    .header_menu{
                        display: none;
                    }
                }
            }

            .header_user{
                p{
                    a {
                        font-size: 14px;


                        &:before {
                            width: 30px;
                            height: 30px;
                            top: -12px;
                            left: -38px;
                        }
                    }
                }
            }
        }
    }

    .main_content {
        background-image: url(img/background.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-color: #ff4656;
        padding: 10px 0 40px;

        .container {
            width: auto;
            margin: 0;

            .main_content_wrapper {
                display: block;

                .main_content_item_text {
                    display: none;
                }

                .main_content_item {
                    width: 100%;
                    margin: 30px 60px 70px;
                    position: relative;

                    img {
                        display: block;
                        //width: 230px;
                        height: 500px;
                        background-color: #1f2326;
                        object-fit: scale-down;
                    }

                    .icons {
                        right: 120px;
                    }
                }
            }
        }

        .carausel_container {
            width: 700px;
            margin-top: 70px;
            margin-left: 8px;
            position: relative;

            .owl-carousel {
                .carausel_item_wrapper {
                    height: 150px;

                    .carausel_item {
                        width: 200px;
                        padding: 0 20px;


                        h4 {
                            font-size: 26px;
                        }

                        h6 {
                            font-size: 16px;
                        }

                        p {
                            font-size: 12px;
                            line-height: 16px;
                        }

                        .carausel_logo {
                            width: 100px;
                            height: 100px;


                            img {
                                width: 80px;
                                height: 80px;
                            }
                        }

                    }
                }

                .owl-prev{
                    display: none;
                }

                .owl-next{
                    display: none;
                }
            }
        }
    }

    .footer{
        background: #F8F8F8;
        padding-top: 60px;
    }
}